.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background: white;
}

.navbar .logo {
  color: #00bfff;
  font-weight: bold;
}

.navbar ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

.navbar ul li a,
.navbar ul li .nav-link {
  text-decoration: none;
  color: black;
  font-weight: 500;
}

.navbar ul li a:hover,
.navbar ul li .nav-link:hover {
  color: #00bfff;
}
